:root {

  --fontSize: 15px;

  --fontSizeLabel: 20px;
}

.InputLabel {
  align-items: flex-end;

  /* background-color: green */
  border-bottom: 2px solid #666;
  grid-column-gap: 5px;
  -webkit-column-gap: 5px;
          column-gap: 5px;
  display: flex;
  flex-flow: row nowrap;
  font-size: 15px;
  font-size: var(--fontSize);
}

.InputLabel-text {
  bottom: 7px;

  /* color: #1b1b1b; */
  color: darkcyan;
  font-size: 20px;
  font-size: var(--fontSizeLabel);
  order: 2;
  pointer-events: none;
  right: 0%;
}

.InputLabel-input {
  background: transparent;

  /* background: green; */
  border: none;
  font-size: 15px;
  font-size: var(--fontSize);
  height: 40px;
  outline: none;
  padding: 10px 10px 0 5px;
  padding-right: 30%;
  width: 75%;
}

.InputLabel-input::-webkit-input-placeholder {
  font-size: 15px;
  font-size: var(--fontSize);
  -webkit-transition: 0.1s ease all;
  transition: 0.1s ease all;
}

.InputLabel-input:-ms-input-placeholder {
  font-size: 15px;
  font-size: var(--fontSize);
  -ms-transition: 0.1s ease all;
  transition: 0.1s ease all;
}

.InputLabel-input::placeholder {
  font-size: 15px;
  font-size: var(--fontSize);
  transition: 0.1s ease all;
}

.InputLabel-input:focus::-webkit-input-placeholder {
  -webkit-transform: translateY(-15px) translateX(-17%) scale(0.7);
          transform: translateY(-15px) translateX(-17%) scale(0.7);
}

.InputLabel-input:focus:-ms-input-placeholder {
  transform: translateY(-15px) translateX(-17%) scale(0.7);
}

.InputLabel-input:focus::placeholder {
  -webkit-transform: translateY(-15px) translateX(-17%) scale(0.7);
          transform: translateY(-15px) translateX(-17%) scale(0.7);
}

/* .InputLabel-input:focus {
  animation: inputBar 100ms linear normal;

  border-bottom: 2px solid tran;
} */

.InputLabel-input:focus > .InputLabel {
  -webkit-animation: inputBar 100ms linear normal;
          animation: inputBar 100ms linear normal;

  /* border-bottom: 2px solid #42a5f5; */
  border-bottom: 2px solid darkcyan;
}

.InputLabel-input:focus:not( :-ms-input-placeholder ) {

  /* color: #0077c1; */
  color: darkcyan;
}

.InputLabel-input:focus:not( :placeholder-shown ) {

  /* color: #0077c1; */
  color: darkcyan;
}

.InputLabel-input:not( :-ms-input-placeholder ) .InputLabel-text {

  /* color: #0077c1; */
  color: darkcyan;
}

.InputLabel-input:not( :placeholder-shown ) .InputLabel-text {

  /* color: #0077c1; */
  color: darkcyan;
}

@-webkit-keyframes inputBar {

  from {
    -webkit-transform: scaleX(0);
            transform: scaleX(0);
  }

  to {
    -webkit-transform: scaleX(1);
            transform: scaleX(1);
  }
}

@keyframes inputBar {

  from {
    -webkit-transform: scaleX(0);
            transform: scaleX(0);
  }

  to {
    -webkit-transform: scaleX(1);
            transform: scaleX(1);
  }
}

:root {

  --dy_in: -100px;

  --dy_out: 100px;

  --phrase_width: 400px;
}

body {
  overflow: hidden;
}

.Login {
  align-items: center;
  background: white;
  display: flex;
  flex-flow: nowrap row;
  height: 100vh;
  justify-content: space-around;
  grid-row-gap: 15px;
  row-gap: 15px;
  width: 100vw;
}

.Login-formContainer {
  display: flex;
  flex-flow: nowrap column;
  grid-row-gap: 15px;
  row-gap: 15px;
  width: 40vw;
}

.Login form, .Login footer {
  display: flex;
  flex-flow: nowrap column;
  grid-row-gap: 15px;
  row-gap: 15px;
  z-index: 2;
}

.Login-modelContainer {
  display: flex;
  flex-flow: nowrap column;
  height: 100vh;
  z-index: 5;
}

.Login form button, .Login footer button {
  align-self: center;
  border: none;
  font-size: 15px;
  height: 60px;
  justify-self: center;
  outline: none;
  padding: 10px;
  transition: 0.4s ease all;
  width: 260px;
}

.Login footer button {
  background: transparent;

  /* border: 1px solid #42a5f5; */
  border: 1px solid darkcyan;
  cursor: pointer;
}

.Login form button:disabled {
  background: rgba(127, 127, 127, 0.3);
}

.Login form button:enabled {

  /* background: #42a5f5; */
  background: darkcyan;
  color: white;
  cursor: pointer;
  font-size: 13px;
}

.Login footer button:hover {
  background: #80d6ff;
  border: solid 1px white;
}

.Login form button:hover:enabled {
  background: #0077c1;
}

/* NOTE animações dos títulos do textos */
.Login-gameTitle {
  display: inline-flex;
}

.Login-gameTitle span {
  color: darkcyan;
  display: block;
  font-family: Rubik Puddles , cursive;
  font-size: 100px;
  font-weight: 200;
  letter-spacing: 0.3em;
}

.Login-gameTitleT {
  animation: titleLetterAnimHor 0.5s ease alternate-reverse infinite;
}

.Login-gameTitleR {
  animation: titleLetterAnimTopBottom 0.5s ease alternate-reverse infinite;
}

.Login-gameTitleI {
  animation: titleLetterAnimHor 0.5s ease alternate-reverse infinite;
}

.Login-gameTitleV {
  animation: titleLetterAnimHor 0.5s ease 0.3s alternate-reverse infinite;
}

.Login-gameTitleA {
  animation: titleLetterAnimTopBottom 0.5s ease alternate-reverse infinite;
}

@-webkit-keyframes titleLetterAnimTopBottom {

  to {
    -webkit-transform: translateY(-20px);
            transform: translateY(-20px);
  }
}

@keyframes titleLetterAnimTopBottom {

  to {
    -webkit-transform: translateY(-20px);
            transform: translateY(-20px);
  }
}

@-webkit-keyframes titleLetterAnimHor {

  to {
    -webkit-transform: rotateY(-40deg);
            transform: rotateY(-40deg);
  }
}

@keyframes titleLetterAnimHor {

  to {
    -webkit-transform: rotateY(-40deg);
            transform: rotateY(-40deg);
  }
}

/* NOTE  cubos do background */

@-webkit-keyframes cube {

  from {
    opacity: 1;
    -webkit-transform: scale(0) rotate(0deg) translate(-50%, -50%);
            transform: scale(0) rotate(0deg) translate(-50%, -50%);
  }

  to {
    opacity: 0;
    -webkit-transform: scale(20) rotate(960deg) translate(-50%, -50%);
            transform: scale(20) rotate(960deg) translate(-50%, -50%);
  }
}

@keyframes cube {

  from {
    opacity: 1;
    -webkit-transform: scale(0) rotate(0deg) translate(-50%, -50%);
            transform: scale(0) rotate(0deg) translate(-50%, -50%);
  }

  to {
    opacity: 0;
    -webkit-transform: scale(20) rotate(960deg) translate(-50%, -50%);
            transform: scale(20) rotate(960deg) translate(-50%, -50%);
  }
}

