/* NOTE informações das mensagens animadas */
.quote_bio {
  align-items: center;

  /* border: 2px solid red; */
  display: flex;
  justify-content: center;
  position: relative;
}

/* .phrase{

} */

.phrase:after {
  content: '\201D';
  font-family: IM Fell English SC , serif;
  font-size: 25pt;
  opacity: 0.5;
}

.phrase:before {
  content: '\201C';
  font-family: IM Fell English SC , serif;
  font-size: 25pt;
  opacity: 0.5;
}

.phrase {
  font-family: Grape Nuts , cursive;
  font-size: 40px;
  line-height: 2em;
  opacity: 0;
  top: -100px;
  top: var(--dy_in);
  width: 400px;
  width: var(--phrase_width);
}

.phrase.phrase0 {
  -webkit-animation: control_text 15s linear infinite;
          animation: control_text 15s linear infinite;
  position: absolute;

  /* background-color: blue; */
  transition: 1s all ease;
}

.phrase.phrase1 {
  -webkit-animation: control_text 15s linear infinite;
          animation: control_text 15s linear infinite;
  -webkit-animation-delay: 5s;
          animation-delay: 5s;

  /* background-color: green; */
  position: absolute;
}

.phrase.phrase2 {
  -webkit-animation: control_text 15s linear infinite;
          animation: control_text 15s linear infinite;
  -webkit-animation-delay: 10s;
          animation-delay: 10s;

  /* background-color: red; */
  position: absolute;
}


@-webkit-keyframes control_text {

  0% {
    opacity: 0;
    top: -100px;
    top: var(--dy_in);
    -webkit-transform: rotateX(45deg);
            transform: rotateX(45deg);
  }

  3.33%, 30.05% {
    opacity: 1;
    top: 0;
    -webkit-transform: rotateX(0deg);
            transform: rotateX(0deg);
  }

  33.38%, 100% {
    opacity: 0;
    top: 100px;
    top: var(--dy_out);
    -webkit-transform: rotateX(45deg);
            transform: rotateX(45deg);
  }
}


@keyframes control_text {

  0% {
    opacity: 0;
    top: -100px;
    top: var(--dy_in);
    -webkit-transform: rotateX(45deg);
            transform: rotateX(45deg);
  }

  3.33%, 30.05% {
    opacity: 1;
    top: 0;
    -webkit-transform: rotateX(0deg);
            transform: rotateX(0deg);
  }

  33.38%, 100% {
    opacity: 0;
    top: 100px;
    top: var(--dy_out);
    -webkit-transform: rotateX(45deg);
            transform: rotateX(45deg);
  }
}

.loader-wrapper {
  background-color: rgb(219, 225, 230);
  display: none;
  flex-flow: row nowrap;
  height: 100%;
  justify-content: center;
  left: 0;
  opacity: 0.7;
  position: fixed;
  top: 0;
  width: 100%;
  z-index: 100;
}

.loader-container {
  display: flex;
  flex-flow: column nowrap;
  left: 40%;
  position: fixed;
  top: 40%;
  z-index: 101;
}

.loader {
  align-self: center;
  -webkit-animation: spin 2s linear infinite;
          animation: spin 2s linear infinite;
  border: 16px solid rgb(246, 246, 246);
  border-radius: 50%;

  /* Troca de cor defido a armonia da aplicação */

  /* border-top: 16px solid rgb(10, 118, 79); */
  border-top: 16px solid darkcyan;
  height: 120px;
  width: 120px;
}

.loading {

  /* color: rgb(10, 118, 79); */
  color: darkcyan;
  font-size: 30px;
  font-weight: 900;
  margin-top: 10px;
}

@-webkit-keyframes spin {

  0% { -webkit-transform: rotate(0deg); transform: rotate(0deg); }

  /* fonte do loader: https://www.w3schools.com/howto/howto_css_loader.asp */
  100% { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}

@keyframes spin {

  0% { -webkit-transform: rotate(0deg); transform: rotate(0deg); }

  /* fonte do loader: https://www.w3schools.com/howto/howto_css_loader.asp */
  100% { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}

