}

/* .Game  */
.GameHeader {
  align-items: center;
  background-color: darkcyan;
  border: 1px solid darkcyan;
  border-radius: 0.8rem;
  box-shadow: 0.2rem 0.3rem 0.8rem 0.2rem rgb(37 37 37);
