  flex-direction: column-reverse;
  height: 250px;
  justify-content: space-between;
  padding: 0.8rem 0;
  width: 232px;
}

.GameHeader section {
  align-items: center;
  display: flex;
  flex-direction: column;
}

.GameHeader section img {
  border-radius: 2rem;
  height: 70px;
  margin: 0.3rem 0 0.4rem;
  width: 70px;
}

.GameHeader .containerPerfil {
