/* No Found */

/* Reset da página */
* {
  font-family: Verdana , Geneva , Tahoma , sans-serif;
  margin: 0;
  padding: 0;
}

.notFound {
  align-items: center;
  background: aliceblue;
  display: flex;
  height: 100vh;
  justify-content: center;
}

.notFound div {
  background-color: rgba(0, 0, 0, 0.9);
  border: 1px solid darkcyan;
  border-radius: 1rem 0.2rem 1.2rem 0.5rem;
  box-shadow: 80px 50px 80px rgba(39, 39, 39, 0.9);
  height: 400px;
  width: 700px;
}

.notFound div h1 {
  color: darkcyan;
  font-size: 50px;
  margin-top: 50px;
  text-align: center;
}

.notFound div p {
  color: darkcyan;
  font-size: 30px;
  margin-top: 50px;
  text-align: center;
}

.notFound span {
  color: darkcyan;
  display: flex;
  font-size: 60px;
  justify-content: center;
  margin-top: 30px;
}

@media screen and ( max-width : 375px ) {

  .notFound {
    border: 1px solid darkcyan;
    box-sizing: border-box;
    height: 100vh;
    width: 90%;
  }

  .notFound div {
    height: 350px;
    width: 300px;
  }

  .notFound div h1 {
    font-size: 30px;
  }

  .notFound div p {
    font-size: 20px;
  }

  .notFound span {
    font-size: 40px;
  }
}

@media screen and ( max-width : 414px ) {

  .notFound {
    border: 1px solid darkcyan;
    box-sizing: border-box;
    height: 100vh;
    width: 90%;
  }

  .notFound div {
    height: 500px;
    padding: 0.7rem;
    width: 350px;
  }

  .notFound div h1 {
    font-size: 40px;
  }

  .notFound div p {
    font-size: 1.6rem;
  }

  .notFound span {
    font-size: 4.5rem;
  }
}


.mainFeedBack {
  align-items: center;
  border: 1px solid blueviolet;
  display: flex;
  height: 100vh;
  justify-content: space-around;
  width: 100%;
}

.headerFeedBack {
  background-color: rgba(0, 0, 0, 0.9);
  border-radius: 0.8rem;
  box-shadow: 0.2rem 0.3rem 0.8rem 0.2rem rgb(39, 39, 39);
  color: aliceblue;
  height: 300px;
  padding: 0.3rem;
  text-align: center;
  width: 300px;
}

.headerFeedBack h1 {
  color: darkcyan;
  padding: 0.8rem;
  text-align: center;
}

.headerFeedBack h4 {
  font-size: 1.2rem;
  margin-bottom: 0.5rem;
}

.headerFeedBack img {
  border-radius: 2rem;
  height: 70px;
  margin: 0.3rem 0 0.4rem;
  width: 70px;

}

.headerFeedBack p {
  margin-bottom: 0.4rem;
}

.headerFeedBack button {
  background-color: rgba(0, 0, 0, 0.3);
  border: none;
  border-radius: 0.5rem 0.3rem;
  box-shadow: 1px 1px 1px 1px rgb(39, 39, 39);
  color: darkcyan;
  cursor: pointer;
  flex-wrap: wrap;
  font-size: 1rem;
  margin: 0 auto;
  outline: none;
  padding: 0.4rem 1rem;
  transition: 0.2s;
}

.headerFeedBack button:hover {
  background-color: darkcyan;
  box-shadow: 1px 1px 1px 1px rgb(39, 39, 39);
  color: rgb(245, 245, 246);
  margin-top: 0.2rem;

}

