  flex-direction: column;
  height: 250px;
  justify-content: space-between;
  padding: 0.8rem 0;
  text-align: center;
  width: 232px;
}

.sectionFeedBack h4 {
  color: aliceblue;
  font-size: 1.2rem;
  margin-bottom: 0.5rem;
}

.sectionSecunFeedBack {
  color: aliceblue;
}

.sectionFeedBack .sectionNumbers {
  color: aliceblue;
}


.buttonFeedBack {
  border: 0.5rem;
  border-radius: 0.2rem;
  color: darkcyan;
  cursor: pointer;
  flex-wrap: wrap;
  font-size: 1rem;
  margin: 0.5rem 0.3rem 0.2rem 0;
  padding: 0.4rem 0.8rem;
  transition: 0.2s;
}


.buttonFeedBack:hover {
  background-color: darkcyan;
  box-shadow: 1px 1px 1px 1px rgb(39, 39, 39);
  color: rgb(245, 245, 246);
  margin-top: 0.2rem;
}

